
.deliver{
    text-align: center;
    align-items: center;
    justify-content: center;
    padding-top:1rem;
    padding-left:1rem;
    opacity: .9;
    background-color: white;
    margin-bottom: 1rem;
    width:50%;
    margin-left: 25%;
    border-radius: 20px;
    border:1px solid orangered;
}

.small__img{
    height:12rem;
    border-radius: 20px;
}

@media (min-width:200px) and (max-width:568px){
    .small__img{
        height:4rem;
        border-radius: 10px;
    }
}

.smart__name:hover{
    color:red;
    transition: 0.5s ease-in-out;
    transform: scale(1.3);
}

.smart__price{
    margin-bottom:1rem ;

}
.smart__price:hover{
    color:red;
    transition: 0.5s ease-in-out;
    transform: scale(1.3);
}

.great__button{
    margin-bottom: 1rem;
    padding-top:.6rem;
    padding-bottom: .6rem;
    padding-left: 2rem;
    padding-right: 2rem;
    border-radius:20px;
    background-color: lightblue;
    color:red;
    font-size:15px;
    font-weight: 700;
}

.great__button:hover{
    color:black;
    background-color:orangered;
    transition: 1s ease-in-out;
}